body{
	font-family: 'PT Sans Narrow', sans-serif;
}

strong{
	font-weight:700;
}

.btn:hover{
	background-color:yellow!important;
	cursor:pointer;
	-webkit-transition: .5s;
}


.shadow{
	-webkit-box-shadow: 0px 4px 10px 4px rgba(0,0,0,0.5);
	box-shadow: 0px 4px 10px 4px rgba(0,0,0,0.5);
}


